/*
(i) Стили будут применяться ко 
всем классам содержащим *__container
Например header__container, main__container и т.д.
Снипет (HTML): cnt
*/
[class*=__container] {
  max-width: 1690px;
  margin: 0 auto;
  padding: 0 16px;
}

@media (max-width: 1750px) {
  [class*=__container] {
    padding: 0 100px;
  }
}

@media (max-width: 1000px) {
  [class*=__container] {
    padding: 0 16px;
  }
}

