  -moz-osx-font-smoothing: grayscale;
}

input,
button,
textarea {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: inherit;
  font-size: inherit;
}

button {
  cursor: pointer;
  color: inherit;
  background-color: inherit;
  font-weight: inherit;
}

a {
  font-weight: inherit;
  color: inherit;
}

a:link,
a:visited {
  text-decoration: none;
}

a:hover {
